@media screen and(max-width: 1200px) {}

@media screen and(max-width: 992px) {

}


@media screen and(max-width: 768px) {

  html {
    font-size: 12px;
  }
}



@media screen and(max-width: 576px) {
  html {
    font-size: 10px;

  }

  body [class*="desktop-hidden"] {
    display: block;
  }

}








